$theme-colors: (
  primary:         #87b4d4,
  secondary:       #264d69,
  primary-light: rgba(135, 180 ,212, 0.15),
  primary-medium-light:  rgba(135, 180 ,212, 0.3),
  success:         #5ed6b3,
  info:            #264d69,
  warning:         #ffad00,
  danger:          #ff0000,
  light:           #87b4d4,
  dark:            #221e1e,
);

$text-muted: #6e6e6e;

///////// BUTTONS ////////

$btn-padding-y: 0.625rem;
$btn-padding-x: 1.25rem;
$btn-font-size: 0.875rem;
$btn-border-radius: .1875rem;


